import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { Link,browserHistory } from 'react-router';

export default class Header extends Component {
    constructor(props){
        super(props)
        this.state = {
            isOpen:false
        }
    }
    static defaultProps = { 
        title: 'Target 博客',
        headerList: [
            {url:"",title:"网站首页"},
            {url:"",title:"关于我"},
            {url:"",title:"模板分享"},
            {url:"",title:"学无止境"},
            {url:"",title:"慢生活"},
            {url:"",title:"留言"}
        ]
    }
    static porpTypes={
        title:PropTypes.number
    }
    handToggle(){
        this.setState({
            isOpen:!this.state.isOpen
        })
    }
    render() {
        var luyou = browserHistory.getCurrentLocation().hash;
        var starlistStyle = {
            display : (window.innerWidth > 768 ? "block" : (this.state.isOpen ? "block" : "none" ) )
        }
        return (
            <div>
                <header>
                    <div id="mnav">
                        <div className="logo"><a href="/">{ this.props.title }</a></div>
                        <h2 id="mnavh" onClick={ ()=>{ this.handToggle() } } className={ this.state.isOpen ? "open":"" }>
                            <span className="navicon"></span>
                        </h2>
                        <ul id="starlist" style={ starlistStyle }>
                            {
                                this.props.headerList.map((item,i)=>(
                                    <li key={ i }>
                                        <Link to={
                                            item.title==="关于我"?'/about':"/"
                                        }  
                                        style={
                                            {color: 
                                                (
                                                    (item.title==="关于我" && luyou==="#/about") ||
                                                    (item.title==="网站首页" && luyou==="#/")
                                                )
                                            ? "red" : "#fff"}
                                        }>
                                            { item.title }
                                        </Link>
                                    </li>
                                ))
                            }
                        </ul>
                    </div>
                </header>
                <div className="line46"></div>
            </div>
        );
    }
}
Header.propTypes = {
    title:PropTypes.string,
    headerList:PropTypes.array
}


