import React, { Component } from 'react';

import wx from '../images/wx.jpg';


class Weixin extends Component {
  
    render() {
        return (
            <div className="weixin">
                <h2 className="ab_title">微信关注</h2>
                <ul>
                    <img src={ wx } alt="" />
                </ul>
            </div>
        );
    }
}
export default Weixin;